@keyframes move {
  100% {
    transform: translate3d(0, 0, 1px)rotate(360deg);
  }
}

ul, ol {
  --marker-width: 1.4em;
  --marker-space-after: .3em;
  margin: 1em 0;
  padding-left: 0;
  list-style: none;
}

li {
  padding-left: calc(var(--marker-width)  + var(--marker-space-after));
  margin-bottom: .7em;
  position: relative;
}

li:before {
  width: var(--marker-width);
  margin-right: var(--marker-space-after);
  display: inline-block;
  position: absolute;
  left: 0;
}

ul > li:before {
  content: "– ";
}

ol {
  counter-reset: ol-li;
}

ol > li {
  counter-increment: ol-li;
}

ol > li:before {
  content: counter(ol-li);
  text-align: center;
}

body {
  color: #fff;
  margin: 0;
}

.content {
  padding-bottom: 3em;
}

:root {
  --text-indent: 1.5rem;
  --column-left: calc(7% + var(--text-indent));
  --column-width: calc(89% - var(--text-indent));
  hyphens: auto;
  --color-bg: #001168;
  --color-one: #1faef579;
  --color-two: #5ae4e486;
  --color-three: #ffa600;
  --color-signal: #0af;
  font-family: Work Sans, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.6;
}

@media (width >= 650px) {
  :root {
    --text-indent: 4rem;
    --column-left: calc(12% + var(--text-indent));
    --column-width: calc(85% - var(--text-indent));
    font-size: 20px;
  }
}

@media (width >= 940px) {
  :root {
    --column-left: calc(18% + var(--text-indent));
    --column-width: calc(75% - var(--text-indent));
    font-size: 21px;
  }
}

@media (width >= 1200px) {
  :root {
    --column-left: calc(30% + var(--text-indent));
    --column-width: calc(55% - var(--text-indent));
  }
}

@media (width >= 1500px) {
  :root {
    font-size: 1.4vw;
  }
}

p {
  margin-top: 0;
  margin-bottom: .7em;
}

h1, h2, h3, h4 {
  margin-top: .9em;
  margin-bottom: .4em;
  line-height: 1.3;
}

h1 em, h1 strong, h2 em, h2 strong, h3 em, h3 strong, h4 em, h4 strong {
  font-style: inherit;
  font-weight: inherit;
  color: var(--color-signal);
}

h1 strong, h2 strong, h3 strong, h4 strong {
  color: var(--color-three);
}

.content :is(h2, h3, h4) {
  margin-left: calc(0rem - var(--text-indent));
}

h1 {
  color: var(--color-three);
  border-left: .3em solid var(--color-three);
  padding-left: .3em;
  font-family: IBM Plex Mono;
  font-size: 8vw;
  font-style: italic;
  font-weight: 400;
  line-height: 1.1;
}

@media (width >= 768px) {
  h1 {
    hyphens: none;
    font-size: 7vw;
    line-height: 1;
  }
}

h2 {
  letter-spacing: .4em;
  word-spacing: -.1em;
  text-transform: uppercase;
  margin-top: 6em;
  margin-bottom: 2em;
  font-family: IBM Plex Mono;
  font-size: 1em;
  font-weight: 700;
}

h3 {
  border-left: .3em solid var(--color-signal);
  hyphens: none;
  color: var(--color-three);
  margin-top: 1.3em;
  padding-left: .3em;
  padding-right: 2em;
  font-family: IBM Plex Mono;
  font-size: 2rem;
  font-style: italic;
  font-weight: 400;
}

h3 strong {
  color: #fff;
}

h4 {
  color: #fff;
  hyphens: none;
  font-size: 1.5rem;
}

.logo {
  text-align: center;
}

.logo__inner {
  letter-spacing: .4em;
  text-transform: uppercase;
  border: .4em solid #fff;
  border-radius: 2em;
  padding-left: .8em;
  padding-right: .6em;
  font-family: IBM Plex Mono;
  font-weight: 700;
}

.background {
  background: var(--color-bg);
  z-index: -1;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

:not(.nodebox--header) > .node-text {
  width: var(--column-width);
  margin-left: var(--column-left);
}

.background:after {
  content: "";
  background-image: linear-gradient(to right, transparent 0, var(--color-bg) var(--column-left), var(--color-bg) calc(var(--column-left)  + var(--column-width)), transparent 100%);
  opacity: .7;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

a {
  color: var(--color-three);
  text-underline-offset: .3em;
  -webkit-text-decoration: underline #fff;
  text-decoration: underline #fff;
  text-decoration-thickness: .15em;
}

a:hover {
  color: var(--color-signal);
}

.nodebox--footer {
