}

.nodebox--footer p *, .nodebox--footer h3 {
  font-size: .8rem;
}

.nodebox--navigation {
  padding-top: 2em;
}

hr {
  background-color: var(--color-three);
  border: none;
  height: .3rem;
  margin-top: 2.2rem;
  margin-bottom: 2.4rem;
}

